// js/core/BasePage.js - „Éô„Éº„Çπ„Éö„Éº„Ç∏„ÇØ„É©„Çπ

import { authManager } from '../modules/authManager.js';
import { supabaseService } from '../services/supabaseService.js';
import { showNotification } from '../utils/helpers.js';
import { handleError } from '../utils/errorHandler.js';

/**
 * „Éô„Éº„Çπ„Éö„Éº„Ç∏„ÇØ„É©„Çπ
 * ÂÖ®„Éö„Éº„Ç∏„ÅßÂÖ±ÈÄö„ÅÆÊ©üËÉΩ„ÇíÊèê‰æõ
 *
 * @class BasePage
 * @version 2.0.0
 * @since 1.0.0
 */
export class BasePage {
  /**
   * „Éô„Éº„Çπ„Éö„Éº„Ç∏„ÅÆ„Ç≥„É≥„Çπ„Éà„É©„ÇØ„Çø
   * @param {Object} options - ÂàùÊúüÂåñ„Ç™„Éó„Ç∑„Éß„É≥
   * @param {string} options.pageName - „Éö„Éº„Ç∏ÂêçÔºà„Ç™„Éó„Ç∑„Éß„É≥Ôºâ
   * @param {boolean} options.requiresAuth - Ë™çË®º„ÅåÂøÖË¶Å„Åã„Å©„ÅÜ„ÅãÔºà„Éá„Éï„Ç©„É´„Éà: trueÔºâ
   */
  constructor(options = {}) {
    this.pageName =
      options.pageName ||
      this.constructor.name.toLowerCase().replace('page', '');
    this.isInitialized = false;
    this.eventListeners = new Map();
    this.requiresAuth = options.requiresAuth !== false; // „Éá„Éï„Ç©„É´„Éà„ÅØtrue
    this.initializationTime = null;
    this.errorCount = 0;
    this.maxRetries = 3;
    this.retryDelay = 1000; // 1Áßí
  }

  /**
   * „Éö„Éº„Ç∏„ÇíÂàùÊúüÂåñ
   * @param {Object} options - ÂàùÊúüÂåñ„Ç™„Éó„Ç∑„Éß„É≥
   * @param {boolean} options.force - Âº∑Âà∂ÂàùÊúüÂåñÔºà„Éá„Éï„Ç©„É´„Éà: falseÔºâ
   * @param {boolean} options.skipAuth - Ë™çË®º„ÉÅ„Çß„ÉÉ„ÇØ„Çí„Çπ„Ç≠„ÉÉ„ÉóÔºà„Éá„Éï„Ç©„É´„Éà: falseÔºâ
   * @returns {Promise<boolean>} ÂàùÊúüÂåñÊàêÂäü„Åã„Å©„ÅÜ„Åã
   */
  async initialize(options = {}) {
    if (this.isInitialized && !options.force) {
      console.warn(`‚ö†Ô∏è Page ${this.pageName} already initialized`);
      return true;
    }

    const startTime = performance.now();
    this.errorCount = 0;

    try {
      console.log(`üîÑ Initializing ${this.pageName} page...`);

      // Ë™çË®ºÁä∂ÊÖã„Çí„ÉÅ„Çß„ÉÉ„ÇØÔºà„Çπ„Ç≠„ÉÉ„Éó„Ç™„Éó„Ç∑„Éß„É≥„Åå„Å™„ÅÑÂ†¥ÂêàÔºâ
      if (!options.skipAuth) {
        await this.checkAuthentication();
      }

      // „Éö„Éº„Ç∏Âõ∫Êúâ„ÅÆÂàùÊúüÂåñÂá¶ÁêÜ
      await this.onInitialize();

      // „Éá„Éº„Çø„ÅÆË™≠„ÅøËæº„Åø
      await this.loadData();

      // „Ç§„Éô„É≥„Éà„É™„Çπ„Éä„Éº„ÅÆË®≠ÂÆö
      this.setupEventListeners();

      this.isInitialized = true;
      this.initializationTime = performance.now() - startTime;

      console.log(
        `‚úÖ ${this.pageName} page initialized successfully (${this.initializationTime.toFixed(2)}ms)`
      );

      // ÂàùÊúüÂåñÂÆå‰∫Ü„Ç§„Éô„É≥„Éà„ÇíÁô∫ÁÅ´
      this.dispatchEvent('pageInitialized', {
        pageName: this.pageName,
        initTime: this.initializationTime,
      });

      return true;
    } catch (error) {
      this.errorCount++;
      console.error(
        `‚ùå Failed to initialize ${this.pageName} page (attempt ${this.errorCount}):`,
        error
      );

      // „É™„Éà„É©„Ç§„É≠„Ç∏„ÉÉ„ÇØ
      if (this.errorCount < this.maxRetries) {
        console.log(`üîÑ Retrying initialization in ${this.retryDelay}ms...`);
        await this.delay(this.retryDelay);
        return await this.initialize({ ...options, force: true });
      }

      this.handleError(error);
      return false;
    }
  }

  /**
   * Ë™çË®ºÁä∂ÊÖã„Çí„ÉÅ„Çß„ÉÉ„ÇØ
   * @returns {Promise<boolean>} Ë™çË®ºÁä∂ÊÖã
   */
  async checkAuthentication() {
    try {
      console.log(
        `üîê Checking authentication for ${this.pageName} page (requiresAuth: ${this.requiresAuth})`
      );

      // Supabase„ÅåÂà©Áî®ÂèØËÉΩ„Åã„ÉÅ„Çß„ÉÉ„ÇØ
      if (!supabaseService.isAvailable()) {
        console.log(
          `üîê Supabase not available for ${this.pageName} page - skipping auth check`
        );
        return true; // Supabase„ÅåÂà©Áî®„Åß„Åç„Å™„ÅÑÂ†¥Âêà„ÅØË™çË®º„ÉÅ„Çß„ÉÉ„ÇØ„Çí„Çπ„Ç≠„ÉÉ„Éó
      }

      const isAuthenticated = await authManager.isAuthenticated();
      console.log(`üîê Authentication result for ${this.pageName}:`, {
        isAuthenticated,
        requiresAuth: this.requiresAuth,
      });

      if (!isAuthenticated && this.requiresAuth) {
        console.log(
          `üîê Authentication required for ${this.pageName} page - redirecting to login`
        );
        showNotification('„É≠„Ç∞„Ç§„É≥„ÅåÂøÖË¶Å„Åß„Åô', 'warning');

        // „É™„ÉÄ„Ç§„É¨„ÇØ„ÉàÂâç„Å´„Ç§„Éô„É≥„Éà„ÇíÁô∫ÁÅ´
        this.dispatchEvent('authRequired', {
          pageName: this.pageName,
          redirectUrl: './index.html',
        });

        // „ÉÜ„Çπ„ÉàÁí∞Â¢É„Åß„ÅØ„Éä„Éì„Ç≤„Éº„Ç∑„Éß„É≥„Çí„É¢„ÉÉ„ÇØ
        if (typeof window !== 'undefined' && window.location) {
          // CIÁí∞Â¢É„Åß„ÅÆJSDOM„Éä„Éì„Ç≤„Éº„Ç∑„Éß„É≥Âà∂Èôê„ÇíÂõûÈÅø
          const isTestEnvironment =
            typeof process !== 'undefined' &&
            (process.env.NODE_ENV === 'test' || process.env.JEST_WORKER_ID);

          if (isTestEnvironment) {
            // „ÉÜ„Çπ„ÉàÁí∞Â¢É„Åß„ÅØ„Éä„Éì„Ç≤„Éº„Ç∑„Éß„É≥„Çí„Çπ„Ç≠„ÉÉ„Éó
            console.log('Navigation skipped in test environment');
            return false;
          }

          try {
            window.location.href = './index.html';
          } catch (error) {
            // JSDOMÁí∞Â¢É„Åß„ÅØlocation.href„ÅÆÁõ¥Êé•Ë®≠ÂÆö„ÅåÂ§±Êïó„Åô„ÇãÂèØËÉΩÊÄß„Åå„ÅÇ„Çã
            // „Åù„ÅÆÂ†¥Âêà„ÅØassign„É°„ÇΩ„ÉÉ„Éâ„Çí‰ΩøÁî®
            if (window.location.assign) {
              window.location.assign('./index.html');
            } else {
              console.warn('Navigation not available in test environment');
            }
          }
        }
        return false;
      }

      console.log(`üîê Authentication check passed for ${this.pageName} page`);
      return isAuthenticated;
    } catch (error) {
      console.error('‚ùå Authentication check failed:', error);
      handleError(error, {
        context: 'BasePage.checkAuthentication',
        showNotification: true,
      });
      return false;
    }
  }

  /**
   * „Éö„Éº„Ç∏Âõ∫Êúâ„ÅÆÂàùÊúüÂåñÂá¶ÁêÜ
   * „Çµ„Éñ„ÇØ„É©„Çπ„Åß„Ç™„Éº„Éê„Éº„É©„Ç§„Éâ
   * @returns {Promise<void>}
   */
  async onInitialize() {
    // „Çµ„Éñ„ÇØ„É©„Çπ„ÅßÂÆüË£Ö
    console.log(`üìÑ ${this.pageName} page-specific initialization`);
  }

  /**
   * „Ç§„Éô„É≥„Éà„É™„Çπ„Éä„Éº„ÅÆË®≠ÂÆö
   * „Çµ„Éñ„ÇØ„É©„Çπ„Åß„Ç™„Éº„Éê„Éº„É©„Ç§„Éâ
   */
  setupEventListeners() {
    // „Çµ„Éñ„ÇØ„É©„Çπ„ÅßÂÆüË£Ö
    console.log(`üéß Setting up event listeners for ${this.pageName} page`);
  }

  /**
   * „Éá„Éº„Çø„ÅÆË™≠„ÅøËæº„Åø
   * „Çµ„Éñ„ÇØ„É©„Çπ„Åß„Ç™„Éº„Éê„Éº„É©„Ç§„Éâ
   * @returns {Promise<any>} Ë™≠„ÅøËæº„Åæ„Çå„Åü„Éá„Éº„Çø
   */
  async loadData() {
    // „Çµ„Éñ„ÇØ„É©„Çπ„ÅßÂÆüË£Ö
    console.log(`üìä Loading data for ${this.pageName} page`);
    return null;
  }

  /**
   * „Ç®„É©„Éº„Éè„É≥„Éâ„É™„É≥„Ç∞
   * @param {Error} error - „Ç®„É©„Éº„Ç™„Éñ„Ç∏„Çß„ÇØ„Éà
   * @param {Object} options - „Ç®„É©„Éº„Éè„É≥„Éâ„É™„É≥„Ç∞„Ç™„Éó„Ç∑„Éß„É≥
   * @param {boolean} options.showNotification - ÈÄöÁü•„ÇíË°®Á§∫„Åô„Çã„Åã„Å©„ÅÜ„Åã
   * @param {boolean} options.showErrorPage - „Ç®„É©„Éº„Éö„Éº„Ç∏„ÇíË°®Á§∫„Åô„Çã„Åã„Å©„ÅÜ„Åã
   */
  handleError(error, options = {}) {
    const {
      showNotification: shouldShowNotification = true,
      showErrorPage: shouldShowErrorPage = true,
    } = options;

    console.error(`‚ùå Error in ${this.pageName} page:`, error);

    // „Ç®„É©„Éº„Éè„É≥„Éâ„É©„Éº„Å´ÂßîË≠≤
    handleError(error, {
      context: `BasePage.${this.pageName}`,
      showNotification: shouldShowNotification,
      severity: 'error',
    });

    // „É¶„Éº„Ç∂„Éº„Å´„Ç®„É©„Éº„ÇíÈÄöÁü•
    if (shouldShowNotification) {
      showNotification('„Éö„Éº„Ç∏„ÅÆË™≠„ÅøËæº„Åø‰∏≠„Å´„Ç®„É©„Éº„ÅåÁô∫Áîü„Åó„Åæ„Åó„Åü', 'error');
    }

    // „Ç®„É©„Éº„Éö„Éº„Ç∏„ÇíË°®Á§∫
    if (shouldShowErrorPage) {
      this.showErrorPage(error);
    }

    // „Ç®„É©„Éº„Ç§„Éô„É≥„Éà„ÇíÁô∫ÁÅ´
    this.dispatchEvent('pageError', {
      pageName: this.pageName,
      error: error.message,
      stack: error.stack,
    });
  }

  /**
   * „Ç®„É©„Éº„Éö„Éº„Ç∏„ÇíË°®Á§∫
   */
  showErrorPage(error) {
    const mainContent = document.getElementById('main-content');
    if (mainContent) {
      mainContent.innerHTML = `
        <div class="min-h-screen flex items-center justify-center">
          <div class="text-center">
            <div class="text-red-500 text-6xl mb-4">
              <i class="fas fa-exclamation-triangle"></i>
            </div>
            <h1 class="text-2xl font-bold text-gray-800 mb-2">„Ç®„É©„Éº„ÅåÁô∫Áîü„Åó„Åæ„Åó„Åü</h1>
            <p class="text-gray-600 mb-4">„Éö„Éº„Ç∏„ÅÆË™≠„ÅøËæº„Åø‰∏≠„Å´ÂïèÈ°å„ÅåÁô∫Áîü„Åó„Åæ„Åó„Åü„ÄÇ</p>
            <div class="space-x-4">
              <button onclick="window.location.reload()" class="bg-blue-500 text-white px-4 py-2 rounded hover:bg-blue-600">
                ÂÜçË™≠„ÅøËæº„Åø
              </button>
              <button onclick="window.location.href='/index.html'" class="bg-gray-500 text-white px-4 py-2 rounded hover:bg-gray-600">
                „Éõ„Éº„É†„Å´Êàª„Çã
              </button>
            </div>
            ${
              typeof process !== 'undefined' &&
              process.env &&
              process.env.NODE_ENV === 'development'
                ? `
              <details class="mt-4 text-left">
                <summary class="cursor-pointer text-sm text-gray-500">„Ç®„É©„ÉºË©≥Á¥∞</summary>
                <pre class="text-xs text-red-600 mt-2 p-2 bg-red-50 rounded">${error.stack}</pre>
              </details>
            `
                : ''
            }
          </div>
        </div>
      `;
    }
  }

  /**
   * „Ç§„Éô„É≥„Éà„É™„Çπ„Éä„Éº„ÇíËøΩÂä†
   */
  addEventListener(element, event, handler) {
    if (element && typeof handler === 'function') {
      element.addEventListener(event, handler);

      // „Ç§„Éô„É≥„Éà„É™„Çπ„Éä„Éº„ÇíË®òÈå≤ÔºàÂæå„Åß„ÇØ„É™„Éº„É≥„Ç¢„ÉÉ„ÉóÁî®Ôºâ
      const key = `${element.id || 'unknown'}_${event}`;
      this.eventListeners.set(key, { element, event, handler });
    }
  }

  /**
   * „Ç§„Éô„É≥„Éà„É™„Çπ„Éä„Éº„ÇíÂâäÈô§
   */
  removeEventListener(element, event, handler) {
    if (element && typeof handler === 'function') {
      element.removeEventListener(event, handler);

      const key = `${element.id || 'unknown'}_${event}`;
      this.eventListeners.delete(key);
    }
  }

  /**
   * ÂÖ®„Ç§„Éô„É≥„Éà„É™„Çπ„Éä„Éº„Çí„ÇØ„É™„Éº„É≥„Ç¢„ÉÉ„Éó
   */
  cleanup() {
    this.eventListeners.forEach(({ element, event, handler }) => {
      element.removeEventListener(event, handler);
    });
    this.eventListeners.clear();
  }

  /**
   * „Éö„Éº„Ç∏„ÇíÁ†¥Ê£Ñ
   */
  destroy() {
    this.cleanup();
    this.isInitialized = false;
    console.log(`üóëÔ∏è ${this.pageName} page destroyed`);
  }

  /**
   * „Éá„Éº„Çø„Çí‰øùÂ≠ò
   */
  async saveData(data) {
    try {
      if (supabaseService.isAvailable()) {
        return await supabaseService.saveData(data);
      } else {
        // „Ç™„Éï„É©„Ç§„É≥ÊôÇ„ÅØ„É≠„Éº„Ç´„É´„Çπ„Éà„É¨„Éº„Ç∏„Å´‰øùÂ≠ò
        return await this.saveToLocalStorage(data);
      }
    } catch (error) {
      console.error('Failed to save data:', error);
      throw error;
    }
  }

  /**
   * „É≠„Éº„Ç´„É´„Çπ„Éà„É¨„Éº„Ç∏„Å´‰øùÂ≠ò
   */
  async saveToLocalStorage(data) {
    const key = `${this.pageName}_data`;
    const existingData = JSON.parse(localStorage.getItem(key) || '[]');
    existingData.push({
      ...data,
      timestamp: new Date().toISOString(),
      id: Date.now().toString(),
    });
    localStorage.setItem(key, JSON.stringify(existingData));
    return existingData[existingData.length - 1];
  }

  /**
   * „Éá„Éº„Çø„ÇíË™≠„ÅøËæº„Åø
   */
  async loadDataFromStorage() {
    try {
      if (supabaseService.isAvailable()) {
        return await supabaseService.loadData(this.pageName);
      } else {
        return await this.loadFromLocalStorage();
      }
    } catch (error) {
      console.error('Failed to load data:', error);
      return [];
    }
  }

  /**
   * „É≠„Éº„Ç´„É´„Çπ„Éà„É¨„Éº„Ç∏„Åã„ÇâË™≠„ÅøËæº„Åø
   */
  async loadFromLocalStorage() {
    const key = `${this.pageName}_data`;
    return JSON.parse(localStorage.getItem(key) || '[]');
  }

  /**
   * „Éö„Éº„Ç∏„ÅÆÁä∂ÊÖã„ÇíÂèñÂæó
   * @returns {Object} „Éö„Éº„Ç∏„ÅÆÁä∂ÊÖã
   */
  getState() {
    return {
      pageName: this.pageName,
      isInitialized: this.isInitialized,
      eventListenersCount: this.eventListeners.size,
      initializationTime: this.initializationTime,
      errorCount: this.errorCount,
      requiresAuth: this.requiresAuth,
    };
  }

  /**
   * „Ç´„Çπ„Çø„É†„Ç§„Éô„É≥„Éà„ÇíÁô∫ÁÅ´
   * @param {string} eventName - „Ç§„Éô„É≥„ÉàÂêç
   * @param {Object} detail - „Ç§„Éô„É≥„ÉàË©≥Á¥∞
   */
  dispatchEvent(eventName, detail = {}) {
    const event = new CustomEvent(eventName, {
      detail: {
        pageName: this.pageName,
        timestamp: new Date().toISOString(),
        ...detail,
      },
    });
    window.dispatchEvent(event);
  }

  /**
   * ÈÅÖÂª∂ÂÆüË°å
   * @param {number} ms - ÈÅÖÂª∂ÊôÇÈñìÔºà„Éü„É™ÁßíÔºâ
   * @returns {Promise<void>}
   */
  delay(ms) {
    return new Promise((resolve) => setTimeout(resolve, ms));
  }

  /**
   * „Éö„Éº„Ç∏„ÅÆ„Éë„Éï„Ç©„Éº„Éû„É≥„ÇπÊÉÖÂ†±„ÇíÂèñÂæó
   * @returns {Object} „Éë„Éï„Ç©„Éº„Éû„É≥„ÇπÊÉÖÂ†±
   */
  getPerformanceInfo() {
    return {
      pageName: this.pageName,
      initializationTime: this.initializationTime,
      errorCount: this.errorCount,
      eventListenersCount: this.eventListeners.size,
      memoryUsage: performance.memory
        ? {
            used: Math.round(performance.memory.usedJSHeapSize / 1024 / 1024),
            total: Math.round(performance.memory.totalJSHeapSize / 1024 / 1024),
            limit: Math.round(performance.memory.jsHeapSizeLimit / 1024 / 1024),
          }
        : null,
    };
  }

  /**
   * „Éö„Éº„Ç∏„ÅÆÂÅ•ÂÖ®ÊÄß„ÉÅ„Çß„ÉÉ„ÇØ
   * @returns {Object} ÂÅ•ÂÖ®ÊÄß„ÉÅ„Çß„ÉÉ„ÇØÁµêÊûú
   */
  healthCheck() {
    const issues = [];

    if (this.errorCount > 5) {
      issues.push('High error count');
    }

    if (this.eventListeners.size > 50) {
      issues.push('Too many event listeners');
    }

    if (this.initializationTime && this.initializationTime > 5000) {
      issues.push('Slow initialization');
    }

    return {
      isHealthy: issues.length === 0,
      issues,
      score: Math.max(0, 100 - issues.length * 20),
    };
  }

  /**
   * „Éö„Éº„Ç∏„ÅÆÊúÄÈÅ©Âåñ„ÇíÂÆüË°å
   * @returns {Promise<void>}
   */
  async optimize() {
    console.log(`üîß Optimizing ${this.pageName} page...`);

    // ‰∏çË¶Å„Å™„Ç§„Éô„É≥„Éà„É™„Çπ„Éä„Éº„ÅÆÂâäÈô§
    this.cleanup();

    // „É°„É¢„É™‰ΩøÁî®Èáè„ÅÆÊúÄÈÅ©Âåñ
    if (window.gc) {
      window.gc();
    }

    console.log(`‚úÖ ${this.pageName} page optimization complete`);
  }
}
